import '@babel/polyfill';
import Es6Promise from 'es6-promise';
import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import axios from 'axios';
import upperFirst from 'lodash/upperFirst';
import camelCase from 'lodash/camelCase';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import '@/font/SiYuan.css';
import mixin from './mixin/mixin.js';
import '@/cookie.js';
import '@/axios.js';
import Material from './components/dialog/material';
import Login from './components/dialog/login';
Es6Promise.polyfill();
Vue.prototype.$material = Material.install;
Vue.prototype.$login = Login.install;
Vue.mixin(mixin);
Vue.use(ElementUI);
Vue.config.productionTip = false;

var requireComponent = require.context( // 
'./components', // 
false, // 
/\.vue$/);

requireComponent.keys().forEach(function (fileName) {
  // 
  var componentConfig = requireComponent(fileName); //  PascalCase 

  var componentName = upperFirst(camelCase( // 
  fileName.split('/').pop().replace(/\.\w+$/, ''))); // 

  Vue.component(componentName, //  `export default` 
  //  `.default`
  // 
  componentConfig.default || componentConfig);
});
new Vue({
  router: router,
  store: store,
  render: function render(h) {
    return h(App);
  }
}).$mount('#app');